import Vue from 'vue'
import Router from 'vue-router'
//
//import  form '@/'
import Login from '@/components/login/login'   //
import Index from '@/components/index/index'   //
import Welcome from '@/components/welcome/welcome'   //
import Welcome2 from '@/components/welcome2/welcome2'   //2
import Message from '@/components/message/message'   //
import Warning from '@/components/warning/warning'   //
import Warninglog from '@/components/warning/warninglog'   //
import Devices from '@/components/devices/devices'   //
import Services from '@/components/servicesmanage/services'   //
import Servicesinfo from '@/components/servicesmanage/servicesinfo/serviceinfo'   //
import OID  from '@/components/oid/oid.vue'   //OID
import InterFace  from '@/components/interface/interfaceSetting.vue'   //
import InterFaceView  from '@/components/interface/InterFaceView.vue'   //
import Thirdparty  from '@/components/thirdparty/thirdparty.vue'   //
import Warninfo from '@/components/servicesmanage/warninfo/warninfo'   //
import Wappush from '@/components/servicesmanage/warninfo/items/wappush.vue'   //CPU
import Cpuuse from '@/components/servicesmanage/warninfo/items/cpuuse.vue'   //CPU
import Memoryuse from '@/components/servicesmanage/warninfo/items/memoryuse'   //
import Connectnumber from '@/components/servicesmanage/warninfo/items/connectnumber'   //
import Servicelog from '@/components/servicesmanage/warninfo/items/servicelog'   //
import serviceConfig from '@/components/servicesmanage/warninfo/items/serviceConfig'   //
import Connectfail from '@/components/servicesmanage/warninfo/items/connectfail'   //cpu
import IPtableInfo from '@/components/servicesmanage/iptableinfo/iptableinfo.vue'   //
import EsInfo from '@/components/servicesmanage/esinfo/esinfo.vue'   //ES
import ES from '@/components/servicesmanage/esinfo/items/es.vue'   //EStable
// import EsInfo from '@components/servicesmanage/esInfo/esinfo.vue'
import IP from '@/components/servicesmanage/iptableinfo/items/IP.vue'   //
import IPrule from '@/components/servicesmanage/iptableinfo/items/IPrule.vue'   //IP
import Custom from '@/components/custom/custom' // 
import Word from '@/components/word/word' // 
import Firewall from '@/components/firewall/firewall' // 
import Params from '@/components/sysparms/sysparams' // 
import Allowance from '@/components/allowance/index.vue' // 


//
const originalReplace = Router.prototype.replace
Router.prototype.replace = function replace(location) {
  return originalReplace.call(this, location).catch(err => err)
}
let routerPush = Router.prototype.push;
Router.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(err => err)
}
Vue.use(Router)
//
export default new Router({
    // mode:'history',
    routes: [
        {
            path: '/', //
            name: 'Login', //
            component: Login,  //
            meta: { title:'', keyword:'', description:'', },
        },
        {
            path: '/index', //
            name: 'Index', //
            component: Index,  //
            meta: { title:'', keyword:'', description:'', },
            redirect:'/index/welcom',
            children:[
                {
                    path: '/index/welcom', //
                    name: 'Welcome', //
                    component: Welcome2,  //
                    meta: { title:'', keyword:'', description:'', },
                },
                {
                    path: '/index/message', //
                    name: 'message', //
                    component: Message,  //
                    meta: { title:'', keyword:'', description:'', },
                },
                {
                    path: '/index/warning', //
                    name: 'warning', //
                    component: Warning,  //
                    meta: { title:'', keyword:'', description:'', },
                },
                {
                    path: '/index/warninglog', //
                    name: 'warninglog', //
                    component: Warninglog,  //
                    meta: { title:'', keyword:'', description:'', },
                },
                {
                    path: '/index/devices', //
                    name: 'devices', //
                    component: Devices,  //
                    meta: { title:'', keyword:'', description:'', },
                },
                {
                    path: '/index/oid', //
                    name: 'oid', //
                    component: OID,  //
                    meta: { title:'OID', keyword:'', description:'', },
                },
                {
                    path: '/index/thirdparty', //
                    name: 'thirdparty', //
                    component: Thirdparty,  //
                    meta: { title:'', keyword:'', description:'', },
                },
                {
                    path: '/index/interface', //
                    name: 'interface', //
                    component: InterFace,  //
                    meta: { title:'', keyword:'', description:'', },
                },
                {
                    path: '/index/interfaceview', //
                    name: 'interfaceview', //
                    component: InterFaceView,  //
                    meta: { title:'', keyword:'', description:'', },
                },
                {
                    path: '/index/services', //
                    name: 'services', //
                    component: Services,  //
                    meta: { title:'', keyword:'', description:'', },
                },
                {
                    path: '/index/services/servicesinfo', //
                    name: 'servicesinfo', //
                    component: Servicesinfo,  //
                    meta: { title:'', keyword:'', description:'', },
                },
                {
                    path: '/index/warninfo', //
                    name: 'warninfo', //
                    component: Warninfo,  //
                    meta: { title:'', keyword:'', description:'', },
                    redirect:'/index/warninfo/wappush',
                    children:[
                        {
                            path: '/index/warninfo/wappush', //
                            name: 'wappush', //
                            component: Wappush,  //
                            meta: { title:'', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/cpuuse', //
                            name: 'cpuuse', //
                            component: Cpuuse,  //
                            meta: { title:'CPU', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/memoryuse', //
                            name: 'memoryuse', //
                            component: Memoryuse,  //
                            meta: { title:'', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/connectnumber', //
                            name: 'connectnumber', //
                            component: Connectnumber,  //
                            meta: { title:'', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/servicelog', //
                            name: 'servicelog', //
                            component: Servicelog,  //
                            meta: { title:'', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/serviceconfig', //
                            name: 'servicelog', //
                            component: serviceConfig,  //
                            meta: { title:'', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/connectfail', //
                            name: 'connectfail', //
                            component: Connectfail,  //
                            meta: { title:'', keyword:'', description:'', },
                        },
                    ]
                },
                {
                    path: '/index/iptableInfo', //
                    name: 'iptableInfo', //
                    component: IPtableInfo,  //
                    meta: { title:'', keyword:'', description:'', },
                    redirect:'/index/iptableInfo/ip',
                    children:[
                        {
                            path: '/index/iptableInfo/ip', //
                            name: 'ip', //
                            component: IP,  //
                            meta: { title:'', keyword:'', description:'', },
                        },
                        {
                            path: '/index/iptableInfo/iprule', //
                            name: 'iprule', //
                            component: IPrule,  //
                            meta: { title:'IP', keyword:'', description:'', },
                        },
                    ]
                },
                {
                    path: '/index/esinfo', //
                    name: 'esinfo', //
                    component: EsInfo,  //
                    meta: { title:'', keyword:'', description:'', },
                    redirect:'/index/esinfo/es',
                    children:[
                        {
                            path: '/index/esinfo/es', //
                            name: 'es', //
                            component: ES,  //
                            meta: { title:'ES', keyword:'', description:'', },
                        },
                    ]
                },
                {
                    path: '/index/custom', //
                    name: 'custom', //
                    component: Custom,  //
                    meta: { title:'', keyword:'', description:'', },
                },
                {
                    path: '/index/word', //
                    name: 'Word', //
                    component: Word,  //
                    meta: { title:'', keyword:'', description:'', },
                },
                {
                    path: '/index/params', //
                    name: 'params', //
                    component: Params,  //
                    meta: { title:'', keyword:'', description:'', },
                },
                {
                    path: '/index/allowance', //
                    name: 'allowance', //
                    component: Allowance,  //
                    meta: { title:'', keyword:'', description:'', },
                },
            ]
        },
    ],
})



